  top: 152px;
  left: 1490px;
}

.facts__ellipse-second {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 240px;
  left: 1490px;
}

.facts__number-first {
  width: 53px;
  height: 53px;
  z-index: 1;
  position: absolute;
  top: 156px;
  left: 1450px;
}

.facts__number-second {
  width: 53px;
  height: 53px;
  z-index: 1;
  position: absolute;
  top: 244px;
  left: 1450px;
}

.facts__spots-first {
  position: absolute;
  top: 211px;
  left: 1455px;
}

.facts__spots-second {
  position: absolute;
  top: 302px;
  left: 1455px;
}

.facts--spots-style {
  text-align: right;
  color: #fff;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.facts--style {
  text-align: right;
