  font-size: 46px;
  font-style: normal;
  font-weight: 400;
  line-height: 53px;
}

.hero__tasty {
  position: absolute;
  bottom: -4px;
  right: 20px;
}

.hamburger {
  width: 30px;
  height: 30px;
  color: #d41443;
  align-items: center;
  display: flex;
}

.button {
