  border: none;
}

.mobile-menu {
  display: none;
  position: relative;
}

.mobile-menu.is-open {
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: #e17992;
  justify-content: center;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.mobile-menu.is-open .mobile-menu__button {
  text-align: center;
  color: #d41443;
  border: none;
  border-radius: 22px;
  align-items: center;
  margin-bottom: 32px;
