  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.mobile-menu.is-open .mobile-menu__button:hover, .mobile-menu.is-open .mobile-menu__button:active {
  color: #fff;
  background-color: #d41443;
  transition-duration: .4s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.mobile-menu.is-open .mobile-menu__pages {
  flex-direction: column;
  gap: 23px;
  list-style-type: none;
  display: flex;
  position: absolute;
  left: 20px;
}

.mobile-menu.is-open .mobile-menu__pages .mobile-menu__link {
  color: #fff;
  align-items: center;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  display: flex;
}

.mobile-menu.is-open .mobile-menu__buyme {
  margin-right: 10px;
}

.button--close-mobile-menu {
  width: 35px;
  height: 35px;
  color: #fff;
  background: none;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu_container {
  margin-top: 60px;
  margin-left: 21px;
  margin-right: 21px;
}

.hero__advertisement-with-pic {
  display: none;
}

